.add-todo{
    display: flex;

}
.add-todo .item{
    display: block;
    border: none;
    padding: 12px 15px;
    font-size: 16px;
    box-sizing: border-box;
}
.add-todo input{
    display: block;
    flex-grow: 4;
}
.add-todo button{
    display: block;
    flex-grow: 1;
    font-weight: bold;
    background-color: rgb(59, 59, 59);
    color: white;
    cursor: pointer;
    transition: 0.1s ease-in-out;
}

.add-todo button:hover{
    background-color: #4B4B4B;
}
.add-todo button:active{
    background-color: #555555;
}