.todo p{
    margin: 0px;
    background-color: rgb(223, 223, 223);
    padding: 10px 0px 12px 0px;
    font-size: 19px;
    text-align: center;
    position: relative;
}
.todo p input{
    float: left;
    position: absolute;
    left: 10px;
    top: 15px;
}
.todo p button{
    float: right;
    border: none;
    display: block;
    padding: 12px;
    width: 80px;
    position: absolute;
    top: 0px;
    right: 8px;
    font-weight: bold;
    color: white;
    font-size: 20px;
    transform: scaleX(1.2);
    background-color: rgb(59, 59, 59);
    cursor: pointer;
    transition: 0.1s ease-in-out;
}
.todo p button:hover{
    background-color: rgb(75, 75, 75);
}